<template>
<section>
<el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="列表分析" name="all">
    </el-tab-pane>
    <el-tab-pane label="金额图表" name="income">
    </el-tab-pane>
    <el-tab-pane label="门诊数图表" name="number">
    </el-tab-pane>
  </el-tabs>
  <!-- 公共头部 -->
  <el-row>
    <!-- <el-col :span="2" class="top-bar-label">筛选字段</el-col>
        <el-col :span="6">
          <el-input suffix-icon="el-icon-search" clearable="" placeholder="请输入挂号序号、就诊号、姓名查询"
           v-model="tableArgs.searchText"></el-input>
        </el-col> -->
        <el-col :offset="8" :span="2" class="top-bar-label">时间范围</el-col>
        <el-col :span="8">
          <el-date-picker
            v-model="datePicker"
            type="daterange"
            align="right"
            unlink-panels
            value-format="yyyy-MM-dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </el-col>
        <el-col :offset="2" :span="3">
          <el-button type="primary" @click="handleSearch" size="medium">查询</el-button>
        </el-col>
    </el-row>
    <!-- 列表 -->
  <el-row style="margin-top: 24px;" v-show="activeName == 'all'">
    <el-col>
      <el-table :data="listData" style="width: 100%">
        <el-table-column prop="deptcode" label="科室编码"></el-table-column>
        <el-table-column prop="deptname" label="科室名称"></el-table-column>
        <el-table-column prop="deptNum" label="门诊数"></el-table-column>
        <el-table-column prop="sumZMoney" :formatter="funNullToZero" label="草药金额" ></el-table-column>
        <el-table-column prop="sumZJMoney" :formatter="funNullToZero" label="针灸金额" ></el-table-column>
        <el-table-column prop="sumWMoney" :formatter="funNullToZero" label="西药金额" ></el-table-column>
        <el-table-column prop="sumInspectionMoney" :formatter="funNullToZero" label="检验金额" ></el-table-column>
        <el-table-column prop="sumCheckMoney" :formatter="funNullToZero" label="检查金额" ></el-table-column>
      </el-table>
    </el-col>
    <el-col>
      <!-- <div :span="24" class="toolbar">
        <el-pagination background layout="total, prev, pager, next, jumper" @current-change="handlePageChange" :current-page="tableArgs.pageNumber" :page-size="tableArgs.pageSize" :total="total" style="float:right;">
        </el-pagination>
      </div> -->
    </el-col>
  </el-row>
 <!-- 金额图表 -->
  <el-row style="margin-top: 24px;" v-show="activeName == 'income'">
    <el-col :span="24">
      <div id="main" style="width: 1000px;height:300px;margin: 20px auto"></div>
    </el-col>
  </el-row>
   <!-- 门诊数图表 -->
  <el-row style="margin-top: 24px;" v-show="activeName == 'number'">
    <el-col :span="24">
      <div id="number" style="width: 1000px;height:300px;margin: 20px auto"></div>
    </el-col>
  </el-row>
</section>
</template>
<script>
import {
  getDoctorTreatmentRecord, // 根据日期段查询医生就诊记录
  getDeptWorkloadByTime, // 科室数据
} from '../../api'
import echarts from 'echarts'
export default {
  data(){
    return{
      listData: [], // 列表数据
      activeName: 'all',
      incomeData: [], // 金额数据
      nameListEchats: [], // 
      herbalmoneyListEchats: [], // 草药金额
      acupuncturemoneyListEchats: [], // 西药金额
      westernmoneyListEchats: [], // 检验金额
      inspectionmoneyListEchats: [], // 检查金额
      checkmoneyListEcharts: [], // 
      echartsOption: {}, // ec 选项
      datePicker: '',
      pickerOptions2: '',
      myChart: null, // echarts 
      total: 0, // 总条数
      tableArgs:{ // 查询参数
        searchText:'',
        pageSize: 999,
        pageNumber: 1,
      }
    }
  },
  methods:{
    handleClick(element){ // 标签选择
        this.funDraw()
    },
    funDraw(){ // 渲染图表
      if(this.listData.length == 0){
        return
      }
      if(this.activeName == 'income'){
        this.funDataToIncom()
        this.funObjToEchatsList()
        this.funEcartsInit()
      }
      if(this.activeName == 'number'){
        this.funDataToIncom()
        this.funObjToEchatsList()
        this.funEcartsNumber()
      }
    },
    funNullToZero(row, column, cellValue, index){
      if(cellValue == null){
        return '0'
      }else{
        return cellValue
      }
    },
    funGetDateOnly(row, column, cellValue, index){ // 日期去掉时间
      if(cellValue){
        return cellValue.split(' ')[0]
      }
    },
    funEcartsInit(){
      this.myChart = echarts.init(document.querySelector("#main"))
      this.myChart.setOption(this.echartsOption)
    },
    funEcartsNumber(){
      let chartNumber = echarts.init(document.querySelector("#number")) 
      let option = {...this.echartsOption}
      option.legend.data = []
      option.series = {
            "name":"科室统计",
            "type":"bar",
            "barWidth": "40",
            "data":[]
        }
      this.incomeData.forEach(element=>{
        option.legend.data.push(element.deptname)
        option.series.data.push(element.deptNum)
      })
      chartNumber.setOption(option)
    },
    funDataToIncom(){ // 列表数据转金额
      this.incomeData = []
      this.listData.forEach(element=>{
        this.incomeData.push({
          deptname: element.deptname,
          sumCheckMoney: element.sumCheckMoney,
          sumInspectionMoney: element.sumInspectionMoney,
          sumWMoney: element.sumWMoney,
          sumZJMoney: element.sumZJMoney,
          sumZMoney: element.sumZMoney,
          deptNum: element.deptNum,
        })
      })
    },
    funObjToEchatsList(){ // 
      this.nameListEchats = []
      this.herbalmoneyListEchats = []
      this.acupuncturemoneyListEchats = []
      this.westernmoneyListEchats = []
      this.inspectionmoneyListEchats = []
      this.incomeData.forEach(element=>{
        this.nameListEchats.push(element.deptname)
        this.herbalmoneyListEchats.push(element.sumZMoney)
        this.acupuncturemoneyListEchats.push(element.sumZJMoney)
        this.westernmoneyListEchats.push(element.sumWMoney)
        this.inspectionmoneyListEchats.push(element.sumInspectionMoney)
        this.checkmoneyListEcharts.push(element.sumCheckMoney)
      })
      this.echartsOption = {
        tooltip: {  show: true  },
        legend: { data: ["草药金额","针灸金额","西药金额","检验金额","检查金额"] },
        xAxis: [{ type : 'category',  data: this.nameListEchats}],
        yAxis: [{type : 'value'}],
        series: [
          {
            "name":"草药金额",
            "type":"bar",
            "data":this.herbalmoneyListEchats
          },{
            "name":"针灸金额",
            "type":"bar",
            "data":this.acupuncturemoneyListEchats
          },{
            "name":"西药金额",
            "type":"bar",
            "data":this.westernmoneyListEchats
          },{
            "name":"检验金额",
            "type":"bar",
            "data":this.inspectionmoneyListEchats
          },{
            "name":"检查金额",
            "type":"bar",
            "data":this.checkmoneyListEcharts
          }
        ]
      }
    },
    handleSearch(){ // 查询
      this.tableArgs.pageNumber = 1
      this.funGetListData()
    },
    handlePageChange(index){ // 页码切换
      this.tableArgs.pageNumber = index
      this.funGetListData()
    },
    funGetListData(){ // 获取列表数据
      let obj =  {     
        userCode: sessionStorage.getItem('usercode'),
        ...this.tableArgs
      }
      if(this.datePicker){
        obj = { 
          beginDate: this.datePicker[0]?this.datePicker[0]: null,
          endDate: this.datePicker[1]?this.datePicker[1]: null,
          ...obj
        }
      }
      getDeptWorkloadByTime(obj).then(res=>{
        if(res.data.code == '0'){
          this.listData = res.data.data
        }else{
          this.listData = []
        }
      })
    },
  },
  mounted(){
   this.funGetListData()
  }
}
</script>
<style scoped>
.top-bar-label{
  line-height: 40px;
  text-align: center;
}
</style>



